'use strict';

require('./Wrap.scss');

let Header = require('./Header/Header');
let Footer = require('./Footer/Footer');

const Wrap = React.createClass({
  render: function() {
    return (
      <div className="wrap">
        <Header />
        {this.props.children}
        <Footer />
      </div>
    );
  }
});

module.exports = Wrap;



